.App-header {
  background-color: rgb(57, 57, 153);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: white;
  font-weight: bold;
}

h1,
h2,
h3,
h4,
h5 {
  color: rgb(57, 57, 153);
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 2rem;
  flex-wrap: nowrap;
}

.flex-column {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.text-representation {
  display: block;
  min-height: 450px;
  overflow-y: scroll;
  overflow-x: hidden;
  word-wrap: break-word;
  margin: 1rem;
}

button,
create-signature::part(button) {
  background-color: rgb(57, 57, 153); /* Green */
  border: none;
  color: white;
  padding: 0.5rem;
  margin: 0.5rem;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
}
